@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/css/font-awesome.css);
/* @import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap'); */

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: linear-gradient(to left, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%); */
  /* background-color: rgb(44, 43, 43); */
  background: linear-gradient(-35deg, rgb(5,5,5) 0%, rgb(65, 63, 63) 100%);
  background-size: cover;
  min-height: 100vh;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@font-face {
  font-family: 'Blanka Regular';
  font-style: normal;
  font-weight: normal;
  src: local('Blanka Regular'), url(/landscape-picker/static/media/Blanka-Regular.7cf6d898.woff) format('woff');
  }

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


h1 {
  /* font-family: 'Pacifico', cursive; */
  font-family: 'Blanka Regular';
  color:  rgba(10, 255, 243, 1);
  letter-spacing: 0.15rem;
}
